<template>
    <div class="person">
        <div class="topBg">
            <img src="../assets/person-bg.png" alt="">
        </div>
        <!---->
        <div class="navigate">
            <ul class="navigateCenter">
                <li :class="{item:true,active:navIndex==1}" @click="navIndex = 1"></li>
                <li :class="{item:true,active:navIndex==2}" @click="navIndex = 2"></li>
                <li :class="{item:true,active:navIndex==3}" @click="navIndex = 3"></li>
                <li :class="{item:true,active:navIndex==4}" @click="navIndex = 4"></li>
            </ul>
        </div>
        <!---->
        <personChange v-show="navIndex==0"></personChange>
        <!---->
        <personDefault @sendNavIndex="sendNavIndex" v-show="navIndex==1"></personDefault>
        <!---->
        <personStrategy v-show="navIndex==2"></personStrategy>
        <!---->
        <personRoute v-show="navIndex==3"></personRoute>
        <!---->
        <personOrder v-show="navIndex==4"></personOrder>
        <!---->
        <smallFoot></smallFoot>
    </div>
</template>

<script>
    /**/
    import personDefault from '../components/person/personDefault'
    /**/
    import personChange from '../components/person/personChange'
    /**/
    import personStrategy from '../components/person/personStrategy'
    /**/
    import personRoute from '../components/person/personRoute'
    /**/
    import personOrder from '../components/person/personOrder'
    /**/
    import smallFoot from '../components/smallFoot'
    export default {
        name: "person",
        components:{
            personChange,
            personDefault,
            personStrategy,
            personRoute,
            personOrder,
            smallFoot
        },
        data(){
            return{
                account:'',
                navIndex:''  /**/
            }
        },
        methods:{
            /**/
            sendNavIndex(value){
                this.navIndex=value;
            },
        },
        created(){
            this.navIndex = this.$route.query.navIndex;
            this.account = this.$store.state.account;
        }
    }
</script>

<style scoped lang="scss">
    .person {
        width: 100%;
        background-color: #ddd;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 999;
        /**/
        .topBg{
           img{
               width: 100%;
           }
        }
        /**/
        .navigate{
            width: 100%;
            height: 60px;
            background-color: #fff;
            border-bottom: 1px solid #eee;
            box-shadow: 0 2px 5px rgba(0,0,0,0.12);
            .navigateCenter{
                width: 60%;
                height: 100%;
                margin: 0 28%;
                padding-left: 0;
                list-style: none;
                line-height: 60px;
                display: flex;
                color: #000;
                font-size: 18px;
                .item{
                    flex: 1;
                    margin-right: 25px;
                    text-align: center;
                    cursor: pointer;
                }
                .active{
                    border-bottom:  2px solid #ff8a00;
                }
            }
        }
    }
</style>